.header{
    margin:0px;
    display: flex;
    justify-content: space-around;
    background-color: #00416A;
    text-align: center;
   
}

.header div {
 
}


.header ul{
display: flex;
}
ul li{ 
    font-size:24px;
    margin-top:12px;
    padding:0px 26px 0px 5px;
    list-style-type: none;
}
.logo{
    padding: 26px;
    background-color: #109030;
}

.navBar{
    display: flex;
    text-align: center;
}

#sort{
    border: none;
    color: transparent;
  text-shadow: 0 0 0 black;
    border-radius: 31px;
    margin-top:20px;
    outline: none;
    height: 24px;
     width: 220px;
    padding: 8px 0px 8px 0px;
    background: rgba(255,25,55,0.7) !important;
     font-size: 24px; 
}
#sort::placeholder {
    color: #123345;
    padding-left: 110px;
}
.line{
    display: none;
    margin-top:50px;
    padding: 1px 0px 0px 0px;
    position: absolute;
    width: 100vw;
    background-color: #123455;
}